import { Fragment } from 'react'

import Card from './Card'

const data = [{
    "id": 1,
    "lang": "Python",
    "url": "https://cdn.svgporn.com/logos/python.svg",
    "fcolor": "#00c3ff",
    "scolor": "#ffff1c"
  },
  {
    "id": 2,
    "lang": "Java",
    "url": "https://cdn.svgporn.com/logos/java.svg",
    "fcolor": "#D31027",
    "scolor": "#EA384D"
  },
  {
    "id": 3,
    "lang": "React",
    "url": "https://cdn.svgporn.com/logos/react.svg",
    "fcolor": "#000428",
    "scolor": "#004e92"
  },
  {
    "id": 4,
    "lang": "Perl",
    "url": "https://cdn.svgporn.com/logos/perl.svg",
    "fcolor": "#cc2b5e",
    "scolor": "#753a88"
  }]

const Cards = () => {
    return (
        <>
        {data.map(({id, lang, url, fcolor, scolor}) => {
            return <Card 
            key={id} 
            lang={lang} 
            img={url} 
            fcolor={fcolor} 
            scolor={scolor} />
        })}            
        </>
    )
}


export default Cards
